
html, body {
    height: 100%;
    box-sizing: border-box;
    margin: 0;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    height: 100%;
}
.companies-view {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    height: 100%;
}
.companies-search > input {
    height: 50px;
    border-bottom: 2px solid red;
    border: none;
    padding: 0;
    width: 100%;
    font-size: 23px;
    border-bottom: 4px solid #d7e5ac;
    background-color: transparent;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    color: #2c3e50;
}
input:focus {
    outline: none;
}
.companies-list {
    width: 250px;
    min-width: 250px;
    height: 100%;
    overflow-y: scroll;
    background: #eee;
    padding: 0 10px;
}
.companies-list > .company-card {
    margin: 10px 0;
}
.company-details {
    -webkit-box-flex: 3;
        -ms-flex: 3;
            flex: 3;
}



